export async function onRequestGet({ request }) {
  const { searchParams } = new URL(request.url);
  const imageUrl = searchParams.get("url");

  if (!imageUrl) {
    return new Response(
      JSON.stringify({ error: "Missing image URL in query parameters" }),
      {
        status: 400,
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      }
    );
  }

  try {
    // Create a new request without conditional headers
    const fetchRequest = new Request(imageUrl, {
      headers: {
        Accept: "image/avif,image/webp,image/apng,image/svg+xml,image/*,*/*;q=0.8",
        "User-Agent":
          "Mozilla/5.0 (iPhone; CPU iPhone OS 16_6 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/16.6 Mobile/15E148 Safari/604.1",
        Referer: "https://masahub2.com/", // Required to bypass hotlinking
      },
    });

    const response = await fetch(fetchRequest);

    if (!response.ok) {
      return new Response(
        JSON.stringify({ error: "Failed to fetch the image" }),
        {
          status: response.status,
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
          },
        }
      );
    }

    // Extract the content type and image buffer
    const contentType = response.headers.get("Content-Type");
    const imageBuffer = await response.arrayBuffer();

    // Serve the image with headers that prevent caching and conditional requests
    return new Response(imageBuffer, {
      status: 200,
      headers: {
        "Content-Type": contentType,
        "Access-Control-Allow-Origin": "*",
        "Cache-Control": "no-store, no-cache, must-revalidate, proxy-revalidate",
        Pragma: "no-cache",
        Expires: "0",
        ETag: "", // Remove ETag to prevent future conditional requests
        "Last-Modified": "", // Remove Last-Modified for the same reason
      },
    });
  } catch (error) {
    return new Response(JSON.stringify({ error: error.message }), {
      status: 500,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    });
  }
}
